// app/blog/[id]/page.tsx
import { useParams } from 'next/navigation';  // 使用 next/navigation 的 useParams
import { useEffect, useState } from 'react';
import Link from 'next/link';

// 模拟的文章数据
const articles = [
  {
    id: 1,
    title: "Understanding React and Tailwind CSS",
    content: "In this article, we'll explore how to integrate React with Tailwind CSS for creating fast, responsive UIs.",
    date: "2025-01-18",
    author: "John Doe"
  },
  {
    id: 2,
    title: "JavaScript ES6 Features You Should Know",
    content: "In this article, we discuss the top ES6 features that every JavaScript developer should master.",
    date: "2025-01-15",
    author: "Jane Smith"
  }
  // 更多文章...
];

const ArticleDetail = () => {
  const { id } = useParams(); // 使用 useParams 获取动态路由参数
  const [article, setArticle] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (id) {
      // 模拟加载文章数据
      const foundArticle = articles.find((article) => article.id.toString() === id);
      if (foundArticle) {
        setArticle(foundArticle);
        setLoading(false);
      } else {
        setLoading(false); // 如果没有找到文章
      }
    }
  }, [id]);

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="loading loading-dots loading-lg"></div>
      </div>
    );
  }

  if (!article) {
    return (
      <div className="text-center my-16">
        <h2 className="text-3xl font-bold">Article Not Found</h2>
        <p className="mt-4 text-lg text-gray-600">Sorry, the article you're looking for does not exist.</p>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto p-8">
      <h1 className="text-4xl font-bold text-center">{article.title}</h1>
      <p className="text-sm text-center text-gray-500 mt-2">{article.date} - By {article.author}</p>
      <div className="mt-8 text-lg text-gray-800 leading-relaxed">
        <p>{article.content}</p>
      </div>
      <div className="mt-8 text-center">
        <Link href="/">
          <a className="btn btn-outline btn-primary">Back to Homepage</a>
        </Link>
      </div>
    </div>
  );
};

export default ArticleDetail;
